"use strict";

var typeahead = require('../../packages/makeup-typeahead');

var list = document.querySelector('ul');
var selected = document.querySelector('.selected');
var TIMEOUT_LENGTH = 2000;

var _typeahead = typeahead(),
    getIndex = _typeahead.getIndex;

function handleKeyDown(e) {
  if (e.key.length === 1) {
    var listIndex = getIndex(list.children, e.key, TIMEOUT_LENGTH);

    if (listIndex !== -1) {
      selected.innerHTML = list.children[listIndex].innerHTML;
    }
  }
}

document.addEventListener('keydown', function (e) {
  return handleKeyDown(e);
});